import bridge from "@vkontakte/vk-bridge";
import {
  APP_ID,
  APP_IMG_SHARING_STORIES,
  GROUP_ID_SUBSCRIPTION_MAIN,
  USER_ID,
} from "../constants";

export function sharing(type, openAlert, e, urlSharing, typeState) {
  switch (type) {
    case "share-link":
      shareLink();
      break;
    case "copy-link":
      copyLink(openAlert);
      break;
    case "story":
      story(urlSharing);
      break;
    case "story-task":
      storyTask(urlSharing, openAlert, typeState);
      break;
    case "share-wall":
      share(e, urlSharing);
      break;
    default:
      break;
  }
}

function shareLink() {
  const link = `https://vk.com/app${APP_ID}`;
  bridge.send("VKWebAppShare", {
    link: link,
  });
}

// –ö–æ–ø–∏—Ä–æ–≤–∞–Ω–∏–µ –≤ –±—É—Ñ–µ—Ä
function copyLink(openAlert) {
  const link = `https://vk.com/app${APP_ID}`;
  bridge
    .send("VKWebAppGetClientVersion")
    .then((result) => {
      console.log(result.platform);
      if (result.platform === "web" || result.platform === "mobile-web") {
        window.navigator.clipboard.writeText(link).then(
          () => {
            console.log("successfully set", link);
            openAlert("–°—Å—ã–ª–∫–∞ —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∞");
          },
          () => {
            console.log("write failed", link);
          }
        );
      } else {
        bridge.send("VKWebAppCopyText", { text: link });
        openAlert("–°—Å—ã–ª–∫–∞ —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∞");
      }
    })
    .catch((error) => {
      console.log("error", error);
    });
}

//  –ü–æ–¥–µ–ª–∏—Ç—å—Å—è –≤ –∏—Å—Ç–æ—Ä–∏–∏
function story(urlSharing) {
  const url = `https://vk.com/app${APP_ID}`;

  bridge.send("VKWebAppShowStoryBox", {
    background_type: "image",
    url: urlSharing,
    attachment: {
      text: "go_to",
      type: "url",
      url: url,
    },
  });
}

//  –ü–æ–¥–µ–ª–∏—Ç—å—Å—è –≤ –∏—Å—Ç–æ—Ä–∏–∏ –ø–æ –∑–∞–¥–∞–Ω–∏—é
function storyTask(urlSharing, openAlert, typeState) {
  const url = `https://vk.com/app${APP_ID}`;
  bridge
    .send("VKWebAppShowStoryBox", {
      background_type: "image",
      url: urlSharing,
      attachment: {
        text: "go_to",
        type: "url",
        url: url,
      },
    })
    .then((res) => {
      console.log("VKWebAppShowStoryBox res", res);
      typeState(true);
    })
    .catch((err) => {
      console.log("VKWebAppShowStoryBox err", err);
      openAlert(`–ß—Ç–æ–±—ã —É–∑–Ω–∞—Ç—å —Ä–µ–∑—É–ª—å—Ç–∞—Ç, –≤—ã–ø–æ–ª–Ω–∏—Ç–µ –¥–∞–Ω–Ω–æ–µ –∑–∞–¥–∞–Ω–∏–µ`, "red");
      typeState(false);
    });
}

//  –î–û–ë–ê–í–õ–ï–ù–ò–ï –†–ï–ü–û–°–¢–ê –ù–ê –°–¢–ï–ù–£ –ü–û–õ–¨–ó–û–í–ê–¢–ï–õ–Ø
function share(e, urlSharing) {
  e.preventDefault();

  const url = `https://vk.com/app${APP_ID}`;
  const urlPhotoWall = `${urlSharing},https://vk.com/app${APP_ID}`;
  const text = `–£–∑–Ω–∞–π –µ—Å–ª–∏ –Ω–µ –±–æ–∏—à—å—Å—è! –ü—Ä–∏–ª–æ–∂–µ–Ω–∏–µ - ${url}`;

  bridge.send("VKWebAppShowWallPostBox", {
    message: text,
    attachments: urlPhotoWall,
  });
}
